'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { useAuth } from '@/context/AuthContext';
import { useLanguage } from '@/context/LanguageContext';
import { 
  FiHome, 
  FiUser, 
  FiCalendar, 
  FiFileText, 
  FiUsers, 
  FiSettings,
  FiLogOut,
  FiMessageCircle,
  FiShoppingBag,
  FiDollarSign
} from 'react-icons/fi';

interface AdminSidebarProps {
  activeSection?: string;
  onSectionChange?: (section: string) => void;
}

const AdminSidebar = ({ activeSection, onSectionChange }: AdminSidebarProps) => {
  const pathname = usePathname();
  const { user, logout } = useAuth();
  const { t } = useLanguage();

  const clinicSection = [
    { href: '/admin/dashboard', label: t('dashboard'), icon: FiHome },
    { href: '/admin/appointments', label: t('appointments'), icon: FiCalendar },
    { href: '/admin/users', label: t('users'), icon: FiUsers },
    { href: '/admin/doctors', label: t('doctors'), icon: FiUser },
    { href: '/admin/prescriptions', label: t('prescriptions'), icon: FiFileText },
    { href: '/admin/chats', label: t('chats'), icon: FiMessageCircle },
  ];

  const financeSection = [
    { href: '/admin/ads', label: t('advertisements'), icon: FiShoppingBag },
  ];

  const menuSections = [
    { title: 'CLINIC', items: clinicSection },
    { title: 'FINANCE', items: financeSection },
  ];

  const getInitials = (name: string) => {
    return name
      ?.split(' ')
      ?.map((n: string) => n[0])
      ?.join('')
      ?.toUpperCase()
      ?.slice(0, 2) || 'A';
  };

  return (
    <div className="fixed left-0 top-0 h-full w-64 bg-white dark:bg-gray-800 shadow-lg z-10 border-r border-gray-200 dark:border-gray-700 transition-colors">
      {/* Logo Section */}
      <div className="p-6 border-b border-gray-200 dark:border-gray-700">
        <div className="flex items-center gap-2">
          <div className="w-10 h-10 rounded-full bg-gradient-to-br from-teal-500 to-indigo-600 flex items-center justify-center text-white font-bold text-lg">
            ðŸ©º
          </div>
          <div>
            <h1 className="text-xl font-bold text-gray-800 dark:text-gray-100">MediWise</h1>
            <p className="text-xs text-gray-500 dark:text-gray-400">Healthcare Management</p>
          </div>
        </div>
      </div>

      {/* Navigation Sections */}
      <nav className="p-4 space-y-6 overflow-y-auto h-[calc(100vh-200px)]">
        {menuSections.map((section) => (
          <div key={section.title}>
            <h3 className="text-xs font-semibold text-gray-500 dark:text-gray-400 uppercase tracking-wider mb-2 px-4">
              {section.title}
            </h3>
            <div className="space-y-1">
              {section.items.map((item) => {
                const Icon = item.icon;
                // Use activeSection if provided, otherwise use pathname
                const isActive = activeSection 
                  ? activeSection === item.href 
                  : (pathname === item.href || pathname?.startsWith(item.href + '/') || pathname?.includes(item.href));
                
                const handleClick = (e: React.MouseEvent) => {
                  // If onSectionChange is provided, use it to update state and URL
                  // This allows for single-page navigation with URL updates
                  if (onSectionChange) {
                    e.preventDefault();
                    onSectionChange(item.href);
                  }
                  // Otherwise, let Link handle navigation normally
                };

                return (
                  <Link
                    key={item.href}
                    href={item.href}
                    onClick={handleClick}
                    className={`flex items-center gap-3 px-4 py-3 rounded-lg transition-colors ${
                      isActive
                        ? 'bg-blue-50 dark:bg-blue-900/30 text-blue-600 dark:text-blue-400 font-medium'
                        : 'text-gray-700 dark:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-700'
                    }`}
                  >
                    <Icon className="text-xl" />
                    <span className="text-sm">{item.label}</span>
                  </Link>
                );
              })}
            </div>
          </div>
        ))}
      </nav>

      {/* Bottom Profile Section */}
      <div className="absolute bottom-0 left-0 right-0 p-4 border-t border-gray-200 dark:border-gray-700 bg-white dark:bg-gray-800 transition-colors">
        <div className="mb-3 px-2">
          <div className="flex items-center gap-3 mb-2">
            <div className="w-8 h-8 rounded-full bg-gradient-to-br from-teal-400 to-indigo-500 flex items-center justify-center text-white text-xs font-bold">
              {getInitials(user?.name || 'Admin')}
            </div>
            <div className="flex-1 min-w-0">
              <p className="text-sm font-medium text-gray-900 dark:text-gray-100 truncate">{user?.name || 'Admin'}</p>
              <p className="text-xs text-gray-500 dark:text-gray-400">Super admin</p>
            </div>
          </div>
        </div>
        <button
          onClick={logout}
          className="w-full flex items-center gap-3 px-4 py-2 rounded-lg text-red-600 dark:text-red-400 hover:bg-red-50 dark:hover:bg-red-900/20 transition-colors text-sm"
        >
          <FiLogOut className="text-lg" />
          <span>Logout</span>
        </button>
      </div>
    </div>
  );
};

export default AdminSidebar;
