'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useNotification } from '@/context/NotificationContext';
import { useRouter } from 'next/navigation';
import AdminSidebar from '@/components/AdminSidebar';
import AdminHeader from '@/components/AdminHeader';
import Loading from '@/components/Loading';
import api from '@/lib/api';
import { format, parseISO } from 'date-fns';
import { FiPlus, FiX, FiTrash2 } from 'react-icons/fi';

export default function AdminAdsPage() {
  const { user, loading: authLoading, logout } = useAuth();
  const { showNotification } = useNotification();
  const router = useRouter();
  const [ads, setAds] = useState<any[]>([]);
  const [filteredAds, setFilteredAds] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [activeTab, setActiveTab] = useState<'all' | 'active' | 'inactive'>('all');
  const [selectedAd, setSelectedAd] = useState<any>(null);
  const [showModal, setShowModal] = useState(false);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [isEditing, setIsEditing] = useState(false);
  const [adForm, setAdForm] = useState({
    title: '',
    description: '',
    imageUrl: '',
    link: '',
    targetAudience: 'all',
    isActive: true,
    startDate: '',
    endDate: '',
  });

  useEffect(() => {
    if (!authLoading && !user) {
      router.push('/login');
      return;
    }
    if (user && user.role !== 'admin') {
      if (user.role === 'patient') {
        router.push('/patient/dashboard');
      } else if (user.role === 'doctor') {
        router.push('/doctor/dashboard');
      } else {
        router.push('/dashboard');
      }
      return;
    }
    if (user && user.role === 'admin') {
      fetchAds();
    }
  }, [user, authLoading, router]);

  useEffect(() => {
    filterAds();
  }, [ads, activeTab, searchQuery]);

  const fetchAds = async () => {
    try {
      setLoading(true);
      const response = await api.get('/admin/ads?limit=1000');
      setAds(response.data.ads || []);
      setFilteredAds(response.data.ads || []);
    } catch (error) {
      console.error('Error fetching ads:', error);
      showNotification('Failed to fetch advertisements', 'error');
    } finally {
      setLoading(false);
    }
  };

  const filterAds = () => {
    let filtered = [...ads];

    if (activeTab === 'active') {
      filtered = filtered.filter((ad) => ad.isActive === true);
    } else if (activeTab === 'inactive') {
      filtered = filtered.filter((ad) => ad.isActive === false);
    }

    if (searchQuery.trim()) {
      const query = searchQuery.toLowerCase();
      filtered = filtered.filter(
        (ad) =>
          ad.title?.toLowerCase().includes(query) ||
          ad.description?.toLowerCase().includes(query)
      );
    }

    setFilteredAds(filtered);
  };

  const handleCreateAd = async () => {
    try {
      await api.post('/ads', adForm);
      showNotification('Advertisement created successfully', 'success');
      setShowCreateModal(false);
      setAdForm({
        title: '',
        description: '',
        imageUrl: '',
        link: '',
        targetAudience: 'all',
        isActive: true,
        startDate: '',
        endDate: '',
      });
      fetchAds();
    } catch (error: any) {
      showNotification(error.response?.data?.message || 'Failed to create advertisement', 'error');
    }
  };

  const handleUpdateAd = async () => {
    try {
      await api.put(`/ads/${selectedAd.id}`, adForm);
      showNotification('Advertisement updated successfully', 'success');
      setShowModal(false);
      setIsEditing(false);
      setSelectedAd(null);
      fetchAds();
    } catch (error: any) {
      showNotification(error.response?.data?.message || 'Failed to update advertisement', 'error');
    }
  };

  const handleDeleteAd = async (adId: string) => {
    if (!confirm('Are you sure you want to delete this advertisement?')) {
      return;
    }

    try {
      await api.delete(`/ads/${adId}`);
      showNotification('Advertisement deleted successfully', 'success');
      fetchAds();
    } catch (error: any) {
      showNotification(error.response?.data?.message || 'Failed to delete advertisement', 'error');
    }
  };

  const openEditModal = (ad: any) => {
    setSelectedAd(ad);
    setAdForm({
      title: ad.title || '',
      description: ad.description || '',
      imageUrl: ad.imageUrl || '',
      link: ad.link || '',
      targetAudience: ad.targetAudience || 'all',
      isActive: ad.isActive !== undefined ? ad.isActive : true,
      startDate: ad.startDate ? format(parseISO(ad.startDate), 'yyyy-MM-dd') : '',
      endDate: ad.endDate ? format(parseISO(ad.endDate), 'yyyy-MM-dd') : '',
    });
    setIsEditing(true);
    setShowModal(true);
  };

  const handleCreateClick = () => {
    setAdForm({
      title: '',
      description: '',
      imageUrl: '',
      link: '',
      targetAudience: 'all',
      isActive: true,
      startDate: '',
      endDate: '',
    });
    setIsEditing(false);
    setSelectedAd(null);
    setShowCreateModal(true);
  };

  if (authLoading || loading) {
    return <Loading />;
  }

  if (!user || user.role !== 'admin') {
    return null;
  }

  return (
    <div className="flex min-h-screen bg-gray-50 dark:bg-gray-900">
      <AdminSidebar />
      <main className="ml-64 flex-1 transition-all duration-300">
        <AdminHeader
          title="Advertisements Management"
          searchPlaceholder="Search advertisements by title or description..."
          onSearch={(query) => setSearchQuery(query)}
          actionButton={{
            label: 'Create Ad',
            onClick: handleCreateClick,
            icon: <FiPlus className="text-lg" />,
          }}
        />
        <div className="p-8">
          {/* Tabs */}
          <div className="mb-6">
            <div className="flex items-center gap-6 border-b border-gray-200 dark:border-gray-700">
              <button
                onClick={() => setActiveTab('all')}
                className={`px-4 py-3 text-sm font-medium border-b-2 transition-colors ${
                  activeTab === 'all'
                    ? 'border-blue-600 text-blue-600 dark:border-blue-400 dark:text-blue-400'
                    : 'border-transparent text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-gray-100'
                }`}
              >
                All ({ads.length})
              </button>
              <button
                onClick={() => setActiveTab('active')}
                className={`px-4 py-3 text-sm font-medium border-b-2 transition-colors ${
                  activeTab === 'active'
                    ? 'border-blue-600 text-blue-600 dark:border-blue-400 dark:text-blue-400'
                    : 'border-transparent text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-gray-100'
                }`}
              >
                Active ({ads.filter((a) => a.isActive).length})
              </button>
              <button
                onClick={() => setActiveTab('inactive')}
                className={`px-4 py-3 text-sm font-medium border-b-2 transition-colors ${
                  activeTab === 'inactive'
                    ? 'border-blue-600 text-blue-600 dark:border-blue-400 dark:text-blue-400'
                    : 'border-transparent text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-gray-100'
                }`}
              >
                Inactive ({ads.filter((a) => !a.isActive).length})
              </button>
            </div>
          </div>

          {/* Ads Grid */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredAds.length === 0 ? (
              <div className="col-span-full bg-white dark:bg-gray-800 rounded-lg shadow-md p-8 text-center border border-gray-200 dark:border-gray-700 transition-colors">
                <p className="text-gray-600 dark:text-gray-400 text-lg">No advertisements found</p>
              </div>
            ) : (
              filteredAds.map((ad) => (
                <div
                  key={ad.id}
                  className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 overflow-hidden hover:shadow-md transition-all"
                >
                  {ad.imageUrl && (
                    <div className="w-full h-48 bg-gray-200 dark:bg-gray-700 overflow-hidden">
                      <img
                        src={ad.imageUrl}
                        alt={ad.title}
                        className="w-full h-full object-cover"
                        onError={(e) => {
                          (e.target as HTMLImageElement).style.display = 'none';
                        }}
                      />
                    </div>
                  )}
                  <div className="p-4">
                    <div className="flex items-start justify-between mb-2">
                      <h3 className="text-lg font-semibold text-gray-800 dark:text-gray-100 flex-1">{ad.title}</h3>
                      <span
                        className={`ml-2 px-2 py-1 rounded-full text-xs font-semibold ${
                          ad.isActive
                            ? 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200'
                            : 'bg-gray-100 text-gray-800 dark:bg-gray-700 dark:text-gray-200'
                        }`}
                      >
                        {ad.isActive ? 'Active' : 'Inactive'}
                      </span>
                    </div>
                    <p className="text-sm text-gray-600 dark:text-gray-400 mb-3 line-clamp-2">{ad.description}</p>
                    <div className="flex items-center justify-between text-xs text-gray-500 dark:text-gray-400 mb-3">
                      <span>Target: {ad.targetAudience || 'all'}</span>
                      <span>Clicks: {ad.clickCount || 0}</span>
                    </div>
                    <div className="flex gap-2">
                      <button
                        onClick={() => openEditModal(ad)}
                        className="flex-1 px-3 py-2 text-sm bg-teal-600 text-white rounded-lg hover:bg-teal-700 transition-colors"
                      >
                        Edit
                      </button>
                      <button
                        onClick={() => handleDeleteAd(ad.id)}
                        className="px-3 py-2 text-sm bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
                      >
                        <FiTrash2 />
                      </button>
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>

          {/* Create/Edit Modal */}
          {(showCreateModal || showModal) && (
            <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4" onClick={(e) => {
              if (e.target === e.currentTarget) {
                setShowModal(false);
                setShowCreateModal(false);
                setIsEditing(false);
                setSelectedAd(null);
              }
            }}>
              <div className="bg-white dark:bg-gray-800 rounded-lg max-w-2xl w-full max-h-[90vh] overflow-hidden flex flex-col transition-colors" onClick={(e) => e.stopPropagation()}>
                <div className="p-6 border-b border-gray-200 dark:border-gray-700 bg-gradient-to-r from-teal-50 to-indigo-50 dark:from-gray-800 dark:to-gray-800">
                  <div className="flex items-center justify-between">
                    <h2 className="text-2xl font-bold text-gray-800 dark:text-gray-100">
                      {isEditing ? 'Edit Advertisement' : 'Create Advertisement'}
                    </h2>
                    <button
                      onClick={() => {
                        setShowModal(false);
                        setShowCreateModal(false);
                        setIsEditing(false);
                        setSelectedAd(null);
                      }}
                      className="p-2 hover:bg-gray-200 dark:hover:bg-gray-700 rounded-lg transition-colors"
                    >
                      <FiX className="text-xl text-gray-600 dark:text-gray-300" />
                    </button>
                  </div>
                </div>

                <div className="overflow-y-auto flex-1 p-6">
                  <div className="space-y-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Title *</label>
                      <input
                        type="text"
                        value={adForm.title}
                        onChange={(e) => setAdForm({ ...adForm, title: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-teal-500 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 transition-colors"
                        placeholder="Enter advertisement title"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Description</label>
                      <textarea
                        value={adForm.description}
                        onChange={(e) => setAdForm({ ...adForm, description: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-teal-500 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 transition-colors"
                        rows={3}
                        placeholder="Enter advertisement description"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Image URL</label>
                      <input
                        type="url"
                        value={adForm.imageUrl}
                        onChange={(e) => setAdForm({ ...adForm, imageUrl: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-teal-500 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 transition-colors"
                        placeholder="https://example.com/image.jpg"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Link URL</label>
                      <input
                        type="url"
                        value={adForm.link}
                        onChange={(e) => setAdForm({ ...adForm, link: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-teal-500 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 transition-colors"
                        placeholder="https://example.com"
                      />
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Target Audience</label>
                        <select
                          value={adForm.targetAudience}
                          onChange={(e) => setAdForm({ ...adForm, targetAudience: e.target.value })}
                          className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-teal-500 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 transition-colors"
                        >
                          <option value="all">All</option>
                          <option value="patient">Patients</option>
                          <option value="doctor">Doctors</option>
                        </select>
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Status</label>
                        <select
                          value={adForm.isActive ? 'true' : 'false'}
                          onChange={(e) => setAdForm({ ...adForm, isActive: e.target.value === 'true' })}
                          className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-teal-500 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 transition-colors"
                        >
                          <option value="true">Active</option>
                          <option value="false">Inactive</option>
                        </select>
                      </div>
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Start Date</label>
                        <input
                          type="date"
                          value={adForm.startDate}
                          onChange={(e) => setAdForm({ ...adForm, startDate: e.target.value })}
                          className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-teal-500 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 transition-colors"
                        />
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">End Date</label>
                        <input
                          type="date"
                          value={adForm.endDate}
                          onChange={(e) => setAdForm({ ...adForm, endDate: e.target.value })}
                          className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-teal-500 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 transition-colors"
                        />
                      </div>
                    </div>
                  </div>
                </div>

                <div className="p-6 border-t border-gray-200 dark:border-gray-700 bg-gray-50 dark:bg-gray-800 flex gap-4 transition-colors">
                  <button
                    onClick={() => {
                      setShowModal(false);
                      setShowCreateModal(false);
                      setIsEditing(false);
                      setSelectedAd(null);
                    }}
                    className="flex-1 px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-700 font-medium text-gray-700 dark:text-gray-300 transition-colors"
                  >
                    Cancel
                  </button>
                  <button
                    onClick={() => {
                      if (isEditing) {
                        handleUpdateAd();
                      } else {
                        handleCreateAd();
                      }
                    }}
                    disabled={!adForm.title.trim()}
                    className={`flex-1 px-4 py-3 bg-teal-600 text-white rounded-lg hover:bg-teal-700 font-medium transition-colors ${
                      !adForm.title.trim() ? 'opacity-50 cursor-not-allowed' : ''
                    }`}
                  >
                    {isEditing ? 'Update' : 'Create'}
                  </button>
                </div>
              </div>
            </div>
          )}
        </div>
      </main>
    </div>
  );
}





